export default {
    layout: "Profile-content",
    data() {
        return {

          trainings: '',
         training: {
          training_name: "",
          duration: "",
          training_year: "",
          institute_name: "",
      },
            errors: '',

        };
    },
    methods: {
        addtraining() {
            this.$axios
                .$post("/training/add", this.training)
                .then((res) => {
                    this.$router.push("/profile/training");
                    this.training = "",
                    this.errors = "",
                    this.$toaster.success(res.message);
                    this.gettraining();
                    
                })
                .catch((err) => {                    
                    this.errors = err.response.data.errors;

                });
        },

    }






};
